import BasicLayout from 'src/components/basic-layout';

// 
const MetadataManagement = () => import('src/views/metadata-management');
const MetadataManagementDetail = () => import('src/views/metadata-management/detail');
const TableCreate = () => import('src/views/metadata-management/table-create');

// 
const TableList = () => import('src/views/data-management/table-list');
// const ContentAnalysis = () => import('src/views/data-management/content-analysis');

// 
// const FileManagement = () => import('src/views/file-management');

// 
const NotFoundPage = () => import('src/views/exception/404');
const ErrorPage = () => import('src/views/exception/500');

export default {
  basicLayout: {
    title: '',
    path: '/main',
    component: BasicLayout,
    children: [
      {
        title: '',
        icon: ' iconfont icon-shuju',
        path: 'data',
        component: TableList
      },
      // {
      //   title: '',
      //   hide: true,
      //   path: 'data/analysis',
      //   component: ContentAnalysis,
      //   meta: {
      //     index: '/main/data'
      //   }
      // },
      {
        title: '',
        icon: ' iconfont icon-yuanshuju',
        path: 'metadata',
        component: MetadataManagement
      },
      {
        title: '',
        icon: '',
        hide: true,
        path: 'metadata/create',
        component: TableCreate,
        meta: {index: '/main/metadata'}
      },
      {
        title: '',
        icon: '',
        hide: true,
        path: 'metadata/detail/:id',
        component: MetadataManagementDetail,
        meta: {index: '/main/metadata'}
      },
      // {
      //   title: '',
      //   icon: ' iconfont icon-wenjianjia1',
      //   path: 'file',
      //   component: FileManagement
      // },
      {
        title: '',
        icon: 'bars',
        path: 'exception',
        hide: true,
        children: [
          {
            title: '404',
            path: '404',
            component: NotFoundPage
          },
          {
            title: '500',
            path: '500',
            component: ErrorPage
          }
        ]
      }
    ]
  }
};



